@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(to left, rgba(7, 27, 82, 1), rgba(0, 128, 128, 1) 100%); */
  /* background: linear-gradient(to right, rgb(116, 44, 51), rgb(80, 77, 82) 100%); */
  background: -webkit-gradient(linear, left top, right top, from(rgb(241, 109, 59)), to(rgb(156, 52, 66)));
  background: linear-gradient(to right, rgb(241, 109, 59), rgb(156, 52, 66));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search {
    font-family: 'Inconsolata', monospace;
}
#robot-name,
#robot-mail {
    font-family: 'Inconsolata', monospace;
}
#m-footer {
    font-family: 'Inconsolata', monospace;
}

#m-footer a {
    text-decoration: none;
    color: white;
    margin-right: 3px;
}

#m-footer a:hover {
    text-decoration: underline;
}
.main-title,
.load {
    font-family: 'Press Start 2P', cursive;
    font-size: 3em;
    color: rgb(255, 155, 120);
}

@media screen and (max-width: 320px) and (min-width: 240px) {
    .main-title,
    .load {
        font-size: 1.2em;
    }

    .search {
        font-size: 15px;
    }
}

@media screen and (max-width: 425px) and (min-width: 320px) {
    .main-title,
    .load {
        font-size: 1.5em;
    }
}

@media screen and (max-width: 768px) and (min-width: 425px) {
    .main-title,
    .load {
        font-size: 2em;
    }
}
